<!-- 审批执行任务 -->
<template>
  <div class="approvalTask">
    <div class="content">
      <el-form ref="ruleForm" :model="queryForm" :inline="true" class="information-form" label-width="150px">
        <el-form-item>
                <el-select v-model="queryForm.applicationStatus" placeholder="请选择执行申请状态"   @change="handleSearch">
                    <el-option v-for="item in applicationStatusType" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-select v-model="queryForm.performChannel" placeholder="请选择执行渠道"   @change="handleSearch">
                    <el-option v-for="item in performChannelType" :key="item.value" :label="item.label" :value="item.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                <el-input v-model.trim="queryForm.idtypeKey" placeholder="请输入营销活动内容关键词"  @keyup.enter.native="handleSearch"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="handleSearch">查询</el-button>
            </el-form-item>
        </el-form>
      <bdaTable
        class="content-table"
        :tableData="taskData"
        :headerColor="taskHeaderColor"
        @operate="taskOperate"
        :isPage="true"
        :pageConfig="pageConfig"
        @changePage="changePage"
      ></bdaTable>
    </div>
    <operateDialog
      v-if="resultDialogVisible"
      :visible.sync="resultDialogVisible"
      :showData="resultDialogData"
      :type="resultDialogType"
    ></operateDialog>
  </div>
</template>

<script>
import Interface from '@/base/mixin/recommend-management';
import bdaTable from "@/components/bda-table/bda-table";
import operateDialog from "./operate-dialog";

export default {
  components: {
    bdaTable,
    operateDialog
  },
  name: "performApplication",
  mixins: [Interface],
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  props: {
    menuType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      //推荐生成任务
      taskData: {},
      taskRes: false,
      taskHeaderColor: "",
      queryForm: {
        relationNumber: '',
        current: 1,
        size: 10,
        total: 0,
        applicationStatus: '',
        performChannel: '',
        idtypeKey: ''
      },
      //分页设置
      pageConfig: {
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //执行结果查看
      resultDialogVisible: false,
      resultDialogType: "",
      resultDialogData: {},
      applicationStatusType: [],
      performChannelType: []
    };
  },
  mounted() {
    this.getTaskData();
  },
  methods: {
    /* 搜索 */
    handleSearch() {
      console.log(this.queryForm, 'this.queryForm')
    },
    //点击任务表格
    taskOperate(type, data) {
      console.log(type, data);
      this.queryForm.relationNumber = data.relationNumber
      if (type === "approval") {
        this.getTaskDetailData(data)
      } else {
        if (type === 'approvalPass') {
          this.approvalResult('1')
        }
        if (type === 'approvalFail') {
          this.approvalResult('2')
        }

        this.resultDialogVisible = false;
      }

    },
    //审批通过/不通过
    approvalResult(val) {
      this.approvalThrough({
        relationNumber: this.queryForm.relationNumber,
        approvalState: val,
      }).then(res => {
        // if(val==='1')  
        // if(val==='2') 
        this.getTaskData()
      })
    },
    //关闭任务模态框
    taskDialogClose() {
      this.taskDialogVisible = false;
    },
    //提交审批
    submitApproval() {
      this.taskDialogVisible = false;
    },
    //改变分页
    changePage(data) {
      this.queryForm.size = data.size
      this.queryForm.current = data.current
      this.getTaskData()
    },
    //获取推荐生成任务列表
    getTaskData() {
      this.taskHeaderColor = "#1899fa";
      let columnNameData = [
        { prop: "relationName", label: "现场营销活动名称", width: 140 },
        { prop: "descibe", label: "执行渠道" },
        {
          prop: "runmode",
          label: "执行方式",
          align: "center",
          width: "120px"
        },
        {
          prop: "Operate",
          label: "推荐客户清单查看与任务排位置",
          align: "center",
          width: "200px",
          type: [
            {
              name: "点击查看与配置",
              action: "approvalPass",
            }
          ]
        },
        { prop: "address", label: "计划执行地点", width: 120 },
        { prop: "exeTime", label: "计划执行时间", width: 120 },
        {
          prop: "proposer",
          label: "操作",
          align: "center",
          status: "true",
          type: [
            {
              name: "[提交申请]",
              action: "approvalPass"
            },
            {
              name: "[确认推荐数据]",
              action: "approvalFail"
            }
          ]
        },
        {
          prop: "status",
          label: "执行申请状态",
          align: "center",
          width: 120,
        //   option: [
        //     {
        //       name: "审批中",
        //       action: "approvalPass",
        //     },
        //     {
        //       name: "确认不通过",
        //       action: "approvalFail",
        //     },
        //     {
        //       name: "执行中",
        //       action: "approvalPass",
        //     },
        //     {
        //       name: "待确认",
        //       action: "approvalFail",
        //     }
        //   ]
        }
      ]
       this.taskData = {
        tableData: [ {
            relationName: '基于违规用电的安全宣贯活动',
            descibe: '',
            runmode: '',
            Operate: '',
            address: '',
            exeTime: '2020-9-30 18:15',
            proposer: ''
        } ],
        columnNameData: columnNameData
     };
      // this.approvalExecutionList({ ...this.queryForm, menuType: this.menuType }).then(res => {
      //   this.pageConfig.total = res.total
      //   this.pageConfig.size = this.queryForm.size
      //   this.pageConfig.current = this.queryForm.current
      //   this.taskData = {
      //     tableData: [
      //       ...res.records
      //     ],
      //     columnNameData: columnNameData
      //   };
      //   this.taskRes = true;
      // }).catch((err) => {
      //   if (err) {
      //     this.taskData = {
      //       tableData: [],
      //       columnNameData: columnNameData
      //     }
      //   }
      // })
    },
    //获取审批任务详细信息
    getTaskDetailData(data) {
      console.log(data, 'data')
      this.selectTaskDetail({
        relationNumber: data.relationNumber,
        targetId: data.targetId,
        recId: data.recId,
        tagId: data.tagId,
      }).then(res => {
        this.resultDialogType = "approval";
        this.resultDialogData = res
        this.resultDialogVisible = true;
      })
    },
  }
};
</script>

<style scoped lang="scss">
.approvalTask {
  width: 100%;
  height: 100%;
  .content {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    width: 100%;
    height: calc(100%);
    background: #ffffff;
    padding: 10px;
    box-sizing: border-box;
    .content-table {
      height: 90%;
    }
  }
  .task-dialog {
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .dialog-content {
      .input {
        width: 80%;
      }
    }
  }
}
</style>